body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

input[type='range'] {
  -webkit-appearance: none;
  margin: 18px 0;
  width: 100%; }

input[type='range']:focus {
  outline: none; }

input[type='range']::-webkit-slider-runnable-track {
  width: 100%;
  height: 8.4px;
  cursor: pointer;
  animate: 0.2s;
  box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
  background: #3071a9;
  border-radius: 1.3px;
  border: 0.2px solid #010101; }

input[type='range']::-webkit-slider-thumb {
  box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
  border: 1px solid #000000;
  height: 36px;
  width: 16px;
  border-radius: 3px;
  background: #ffffff;
  cursor: pointer;
  -webkit-appearance: none;
  margin-top: -14px; }

input[type='range']:focus::-webkit-slider-runnable-track {
  background: #367ebd; }

input[type='range']::-moz-range-track {
  width: 100%;
  height: 8.4px;
  cursor: pointer;
  animate: 0.2s;
  box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
  background: #3071a9;
  border-radius: 1.3px;
  border: 0.2px solid #010101; }

input[type='range']::-moz-range-thumb {
  box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
  border: 1px solid #000000;
  height: 36px;
  width: 16px;
  border-radius: 3px;
  background: #ffffff;
  cursor: pointer; }

input[type='range']::-ms-track {
  width: 100%;
  height: 8.4px;
  cursor: pointer;
  animate: 0.2s;
  background: transparent;
  border-color: transparent;
  border-width: 16px 0;
  color: transparent; }

input[type='range']::-ms-fill-lower {
  background: #2a6495;
  border: 0.2px solid #010101;
  border-radius: 2.6px;
  box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d; }

input[type='range']::-ms-fill-upper {
  background: #3071a9;
  border: 0.2px solid #010101;
  border-radius: 2.6px;
  box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d; }

input[type='range']::-ms-thumb {
  box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
  border: 1px solid #000000;
  height: 36px;
  width: 16px;
  border-radius: 3px;
  background: #ffffff;
  cursor: pointer; }

input[type='range']:focus::-ms-fill-lower {
  background: #3071a9; }

input[type='range']:focus::-ms-fill-upper {
  background: #367ebd; }

* {
  box-sizing: border-box; }

body {
  background-color: lightskyblue; }

.carousel {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-self: center; }
  .carousel__slider {
    overflow: hidden;
    border: 3px solid lightcoral;
    background-color: lightpink;
    border-radius: 5px; }
  .carousel__slides-wrapper {
    width: 6666px;
    position: relative;
    flex-direction: row; }
  .carousel__slider-wrapper {
    display: flex; }
  .carousel__control-panel {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 1em;
    color: lightcoral;
    font-weight: bold;
    text-shadow: black 1px 1px 0, black -1px -1px 0, black -1px 1px 0, black 1px -1px 0; }
  .carousel__image {
    display: block;
    float: left; }
  .carousel__buttons {
    background-color: lightblue;
    border-radius: 5px;
    border: 1px #3071a9 solid; }
    .carousel__buttons:hover {
      cursor: pointer;
      background-color: #3071a9; }

